:root {
  --primary-color: #D4AF37; /* 上品なゴールド */
  --secondary-color: #3A3B3C; /* 濃いチャコールグレー */
  --accent-color: #F5F5DC; /* ベージュに近いオフホワイト */
  --text-color: #EAEAEA; /* 明るいグレーのテキスト */
  --bg-color: #1C1C1C; /* ほぼ黒に近い背景 */
  --white-color: #ffffff;
  --dark-gray-color: #A9A9A9; /* やや明るいグレー */
}

body {
  margin: 0;
  font-family: 'Georgia', 'Times New Roman', Times, serif; /* 洗練されたセリフ体 */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--text-color);
  background-color: var(--bg-color);
  transition: background-color 0.5s ease;
}

.App {
  opacity: 0;
  animation: fadeIn 1s ease-in forwards;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Header */
.App-header {
  background-color: rgba(28, 28, 28, 0.85); /* 半透明の背景 */
  backdrop-filter: blur(10px);
  padding: 20px 0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.5);
  position: sticky;
  top: 0;
  z-index: 1000;
  border-bottom: 1px solid var(--primary-color);
}

.App-header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.App-header h1 {
  margin: 0;
  font-size: 1.8em;
  color: var(--primary-color);
  font-weight: 700;
  letter-spacing: 1px;
}

.App-header nav a {
  margin-left: 30px;
  text-decoration: none;
  color: var(--accent-color);
  font-weight: 400;
  transition: color 0.3s ease, text-shadow 0.3s ease;
  position: relative;
  padding-bottom: 5px;
}

.App-header nav a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 1px;
  bottom: 0;
  left: 50%;
  background-color: var(--primary-color);
  transition: all 0.3s ease-in-out;
}

.App-header nav a:hover {
  color: var(--primary-color);
  text-shadow: 0 0 5px rgba(212, 175, 55, 0.7);
}

.App-header nav a:hover::after {
  width: 100%;
  left: 0;
}

/* Main Content */
main > section {
  padding: 100px 0;
  border-bottom: 1px solid #2a2a2a;
}

main > section:last-of-type {
  border-bottom: none;
}

h2 {
  text-align: center;
  font-size: 2.8em;
  color: var(--primary-color);
  margin-bottom: 70px;
  font-weight: 700;
  letter-spacing: 1px;
}

/* Hero Section */
.hero {
  background: linear-gradient(rgba(28, 28, 28, 0.8), rgba(28, 28, 28, 0.8)), url('https://source.unsplash.com/random/1600x900/?business,office') no-repeat center center/cover;
  color: var(--white-color);
  text-align: center;
  padding: 120px 0;
}

.hero h2 {
  color: var(--white-color);
  font-size: 3.5em;
  margin-bottom: 20px;
  text-shadow: 0 2px 10px rgba(0,0,0,0.7);
}

.hero .subtitle {
  font-size: 1.8em;
  font-weight: 300;
  margin-bottom: 30px;
  opacity: 0.9;
  color: var(--accent-color);
}

.hero p {
  max-width: 800px;
  margin: 0 auto;
  line-height: 1.8;
  font-size: 1.1em;
}

/* Services Section */
.service-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 40px;
}

.service-item {
  background-color: var(--secondary-color);
  padding: 40px;
  border-radius: 4px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.5);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border-top: 4px solid transparent;
  border-image: linear-gradient(to right, var(--primary-color), #a17d2a) 1;
}

.service-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0,0,0,0.7);
}

.service-item h3 {
  font-size: 1.6em;
  color: var(--primary-color);
  margin-top: 0;
  margin-bottom: 20px;
}

.service-item p {
  line-height: 1.8;
  color: var(--dark-gray-color);
}

/* Company Profile Section */
.company-profile table {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  border-collapse: collapse;
  background-color: var(--secondary-color);
  box-shadow: 0 10px 30px rgba(0,0,0,0.5);
  border-radius: 4px;
  overflow: hidden;
}

.company-profile th, .company-profile td {
  padding: 25px;
  text-align: left;
  border-bottom: 1px solid #2a2a2a;
}

.company-profile tr:last-child th,
.company-profile tr:last-child td {
  border-bottom: none;
}

.company-profile th {
  background-color: #2a2a2a;
  font-weight: 600;
  width: 30%;
  color: var(--primary-color);
}

/* Contact Section */
.contact {
  text-align: center;
}

.contact p {
  font-size: 1.2em;
  margin-bottom: 40px;
  color: var(--dark-gray-color);
}

.cta-button {
  display: inline-block;
  background: linear-gradient(to right, var(--primary-color), #a17d2a);
  color: var(--bg-color);
  padding: 18px 50px;
  font-size: 1.2em;
  font-weight: 700;
  text-decoration: none;
  border-radius: 50px;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(212, 175, 55, 0.3);
  letter-spacing: 1px;
}

.cta-button:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 10px 25px rgba(212, 175, 55, 0.5);
}

/* Footer */
.App-footer {
  background-color: #111;
  color: var(--dark-gray-color);
  text-align: center;
  padding: 40px 0;
  margin-top: 80px;
  border-top: 1px solid #2a2a2a;
}

/* Responsive */
@media (max-width: 768px) {
  .App-header .container {
    flex-direction: column;
  }
  .App-header h1 {
    margin-bottom: 20px;
  }
  .App-header nav a {
    margin: 0 15px;
  }
  h2 {
    font-size: 2.2em;
  }
  .hero h2 {
    font-size: 2.5em;
  }
  .hero .subtitle {
    font-size: 1.4em;
  }
}
